import Layout from "../components/Layout";
import Link from "next/link";

const ProfileLink = props => (
    <div>
        <Link href ={`/profile?name=${props.name}`}>
            <a> go to {props.name}'s profiles </a>
         </Link>
    </div>
);

const Index = () => (
  <div>
    <Layout>
      <p>index page</p>
      <profileLink name="juhee" />
      <profileLink name="minju" />
      <profileLink name="jawon" />
    </Layout>
  </div>
);

export default Index;
